// 引入vue
import Vue from 'vue';
import VueRouter from 'vue-router';
import app from './app';
import vheader from 'component/mui/Header';
import api from 'api';

Vue.use(VueRouter);
var router = new VueRouter();
// head
var HeadVue = Vue.extend(vheader);
new HeadVue({
    el: 'vheader',
    propsData: {
        title: "找回密码",
        statusbarcolor: '#fff',
        hdstyle: {
            back: {
                display: true
            },
            title: {
                display: true
            }
        }
    }
});

//mui生命周期管理
mui.plusReady(function() {
    router.start(app, '#app');
});